@import url(https://fonts.googleapis.com/css?family=Gamja+Flower&display=swap);
@import url(https://fonts.googleapis.com/css?family=Londrina+Shadow|Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css?family=Gamja+Flower&display=swap);
@import url(https://fonts.googleapis.com/css?family=Londrina+Shadow|Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbar-container{
    width: 100vw;
    height: 17vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 100;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: beige;
}

.navbar-title {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3.5rem;
    height: 4vh;
    z-index: 101;
}

.navbar-second-title {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    z-index: 101;
    height: 4vh;
}

.navbar-content {
    width: 100%;
    display: flex;
    justify-content: center;
    height: 5vh;
}

.navbar-content-items {
    margin: 1%;
    font-size: 1.5rem;
    width: 8vw;
}

.opacity{
    height: 0vh;
    opacity: 0;
}

.mini{
    height: 7vh;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    -webkit-transition: 3s, ease;
    transition: 3s, ease;
}

a{
    text-decoration: none;
    color: black;
    margin: 1%;
}
.global-font-kor {
    font-family: 'Gamja Flower', cursive;
}

.global-font-eng {
    font-family: 'Londrina Shadow', cursive;
}

.global-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.global-title {
    
}

.greeting-title {
    font-size: 3.5rem;
    height: 8vh;
    width: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Gamja Flower', cursive;
    border-right: 1px solid black;

}
 
.greeting-cursor {
    height: 8vh;
}
.cursor-effect {
    border-right: 1px solid black;
}
.profile-container {
    border-radius: 100%;
    background-color: gray;
    width: 20vw;
    height: 20vw;
    display: flex;
    justify-content: center;
}

.profile-picture {
    border-radius: 100%;
    background-color: gray;
    width: 26vw;
    height: 20vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile-picture img {
    border-radius: 100%;
    background-color: gray;
    width: 26vw;
    height: 20vw;
    overflow: hidden;
}

.profile-content {
    width: 100%;
    height: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transition: all 1s;
    transition: all 1s;
    flex-direction: column;
}

.change-div {
    width: 100%;
    height: 18vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Gamja Flower', cursive;
    font-size: 2rem;
    -webkit-transition: background-color 2s ease-out;
    transition: background-color 2s ease-out;
}

.change-div1 {
    width: 100%;
    height: 18vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Gamja Flower', cursive;
    font-size: 2rem;
    color: black;
    background-color: none;
    -webkit-transition: background-color 2s ease-out;
    transition: background-color 2s ease-out;
}


.footer-container{
    width: 100%;
    height: 8vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: beige;
    color: gray;
    opacity: 0.5;
}

