.techstack-container {
    width: 75%;
    height: 75%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.tech-row {
    width: 100%;
    height: 20%;
    display: flex;
}

.tech-column {
    width: 18vw;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Londrina Shadow', cursive;
    font-size: 3rem;
    margin: 3%;
}

.tech-column:hover {
    cursor: pointer;
    font-size: 3.5rem;
    -webkit-transition: background-color 2s ease-out;
    -webkit-transition: font-size 1s ease-out;
    transition: font-size 1s ease-out;
}

.render-div {
    font-family: 'Gamja Flower', cursive;
    width: 20vw;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 4rem;
    margin: 3%;
}

.hover-enter{
    color: black;
}

.hover-out {
    color: white;
}
.global-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 1;
}
.global-container1 {
    width: 100vw;
    height: 220vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.project-container {
    width: 80%;
    height: 65vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.project-picture {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
}
.project-picture img {
    width: 100%;
    height: auto;
}

.project-content {
    width: 45%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.project-title {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 3%;
}

.project-explain {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    text-align: start;
    font-size: 1.5rem;
}

.project-anchor{
    color: gray;
    opacity: 0.7;
}
.social-title {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20%;
    height: 20vh;
    font-size: 3.5rem;
}

.social-title:hover {
    cursor: pointer;
    font-size: 4.5rem;
}

.social-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}
.contact-container{
    width: 100%;
    height: 10vh;
    font-size: 3.5rem;
}

.contact-container:hover{
    font-size: 4.5rem;
}
